import React from 'react';

import styles from './Company.module.scss';

const data = [
  'Eureka is a collaborative project that unites some of the most experienced and skilled software developers in the IT industry in Eastern Europe.',
  'From our headquarters in London, you get direct access to over 230 highly qualified specialists from Belarus, Estonia, Lithuania and Ukraine. With the added benefit of being based in London, we are never more than a stone’s throw away.',
  'Having successfully accomplished over 300+ projects, you will benefit from our collective experience and improved methodology processes making our work more efficient and cost-effective. This provides you with better transparency and control over your project development.',
  'Thanks to our ability to rapidly expand team size and attract unique skills, Eureka’s is able to consistently provide top-quality services as well as our utmost attention to any project regardless of size or complexity.'
];

const dataMobile = [
  'Eureka is a collaborative project that unites some of the most experienced and skilled software developers in the IT industry in Eastern Europe.',
  'From our headquarters in London, you get direct access to over 230 highly qualified specialists from Belarus, Estonia, Lithuania and Ukraine. ',
  'Having successfully accomplished over 300+ projects, you will benefit from our collective experience and improved methodology processes making our work more efficient and cost-effective.',
];

const Company = ({ mobile }) => {
  const showData = mobile ? dataMobile : data;

  return (
    <section className={styles.wrapper} id="company">
      <div className={styles.content}>
        <h2 className={`${styles.title} ${styles.sectionTitle}`}>Company</h2>
        <div>
          {showData.map((item, i) => (
            <p className={styles.item} key={i}>{item}</p>
          ))}
        </div>
      </div>
      <div className={styles.bg}/>
    </section>
  )
};

export default Company;
