import React from 'react';

import styles from './DidYouKnow.module.scss';

const data = [
  {
    count: '180+',
    title: 'Clients served',
    text: 'We approach every project individually with a focus on our clients’ specific requirements; be it a start-up, medium-sized or large company.',
  },{
    count: '580+',
    title: 'Completed projects',
    text: 'From customization of existing platforms to full-cycle development of custom software, Eureka provides reliable and cost-effective software solutions.',
  },{
    count: '230+',
    title: 'Full-Time Employees',
    text: 'We are a united team of talented and experienced developers, testers and IT architects, business analysts and IT consultants',
  },{
    count: 5,
    title: 'Locations',
    text: 'The local representation in Western and Eastern Europe is important for immediate assistance and information exchange.',
  },{
    count: '20+',
    title: 'Countries served',
    text: 'Each country is unique in terms of applied tools, technologies, process workflow and communications. We are able and willing to adapt.',
  },{
    count: '10+',
    title: 'Years of Experience',
    text: 'Previous experience has enabled Eureka to become truly effective and cost-efficient as it helps better understand our clients’ needs.',
  },
];

const DidYouKnow = () => (
  <section className={styles.wrapper}>
    <div className={styles.content}>
      <h2 className={`${styles.title} ${styles.sectionTitle}`}>Did you know?</h2>
      <div className={styles.list}>
        {data.map(({count, title, text}, i) => (
          <div className={styles.item} key={i}>
            <p className={styles.itemCount}>{count}</p>
            <p className={styles.itemTitle}>{title}</p>
            <p className={styles.itemText}>{text}</p>
          </div>
        ))}
      </div>
    </div>
  </section>
);

export default DidYouKnow;
