import React from 'react';

import Facebook from '../../images/social/Facebook_black.svg';
import Twitter from '../../images/social/Twitter_black.svg';
import LinkedIN from '../../images/social/LinkedIN_black.svg';
import Instagram from '../../images/social/Instagram_black.svg';
import styles from './Footer.module.scss';

const data = [
  {
    link: 'https://www.facebook.com/',
    img: Facebook,
  },
  {
    link: 'https://twitter.com/',
    img: Twitter,
  },
  {
    link: 'https://www.linkedin.com/',
    img: LinkedIN,
  },
  {
    link: 'https://www.instagram.com/',
    img: Instagram,
  },
];

const Footer = () => (
  <footer>
    <button className={styles.logoWrap} onClick={() => window.scrollTo(0,0)}>
      <span className={styles.burger} />
      <span className={styles.logoTitle}>Ureka</span>
    </button>
    <div className={styles.socialWrap}>
      <ul className={styles.listWrap}>
        {data.map(({link, img}, i) => (
          <li className={styles.item} key={i}>
            <a className={styles.link} href={link} target="_blank" rel="noopener noreferrer">
              <img src={img} alt={`icon - ${img}`}/>
            </a>
          </li>
        ))}
      </ul>
      <span>© Eureka OS Ltd. All rights reserved</span>
    </div>
  </footer>
);

export default Footer;
