import React, { useState, createRef } from 'react';
import emailjs from 'emailjs-com';

import styles from './Contacts.module.scss';

const Contacts = () => {
  const [firstName, onFirstName] = useState('');
  const [firstNameFocus, onFirstNameFocus] = useState(false);
  const [lastName, onLastName] = useState('');
  const [lastNameFocus, onLastNameFocus] = useState(false);
  const [email, onEmail] = useState('');
  const [emailFocus, onEmailFocus] = useState(false);
  const [phone, onTel] = useState('');
  const [phoneFocus, onPhoneFocus] = useState(false);
  const [text, onText] = useState('');
  const [textFocus, onTextFocus] = useState(false);

  const [sendSuccess, onSendSuccess] = useState(0);
  const [checkbox, onCheckbox] = useState(false);

  const firstNameRef = createRef();
  const lastNameRef = createRef();
  const emailRef = createRef();
  const phoneRef = createRef();
  const textRef = createRef();

  const handleChange = () => {
    onFirstName(firstNameRef.current.value);
    onLastName(lastNameRef.current.value);
    onEmail(emailRef.current.value);
    onTel(phoneRef.current.value);
    onText(textRef.current.value);
  }

  const  sendEmail = (e) => {
    e.preventDefault();

    onFirstName('');
    onLastName('');
    onEmail('');
    onTel('');
    onText('');

    emailjs.sendForm('gmail', 'template_YyRAtRML', e.target, 'user_PhWDq1PqpQGNW4l2XcIF9')
      .then(
        result => onSendSuccess(1),
        error => onSendSuccess(2),
      );

      setTimeout(() => onSendSuccess(0), 3000);
  }

  return (
    <section className={styles.wrapper} id="contact">
      <div className={styles.content}>
        <div className={styles.contactsWrap}>
          <h2 className={`${styles.title} ${styles.sectionTitle}`}>Contacts</h2>
          <div className={styles.address}>
            <p className={styles.street}>Eureka OS Ltd.</p>
            <p>15 Half Moon Street, W1J 7DZ</p>
          </div>
          <div className={styles.contacts}>
            <a href="mailto:support@eurekaos.com">support@eurekaos.com</a>
            <a href="phone:+447859071853">+44 7859071853</a>
          </div>
        </div>
        <div className={styles.contactFormWrap}>
          <form className={styles.contactForm} onSubmit={sendEmail}>
            <div className={styles.formLine}>
              <div className={styles.formGroup}>
                <label className={`${styles.label} ${firstNameFocus || firstName.length ? styles.labelFocus : ''}`} htmlFor="firstName">Name</label>
                <input 
                  className={styles.input}
                  ref={firstNameRef} 
                  value={firstName} 
                  onChange={handleChange} 
                  onFocus={() => onFirstNameFocus(true)}
                  onBlur={() => onFirstNameFocus(false)}
                  type="text" 
                  name="firstName"
                  required
                  aria-label="First name"
                />
              </div>
              <div className={styles.formGroup}>
                <label className={`${styles.label} ${lastNameFocus || lastName.length ? styles.labelFocus : ''}`} htmlFor="lastName">Surname</label>
                <input 
                  className={styles.input}
                  ref={lastNameRef} 
                  value={lastName} 
                  onChange={handleChange} 
                  onFocus={() => onLastNameFocus(true)}
                  onBlur={() => onLastNameFocus(false)}
                  type="text" 
                  name="lastName"
                  required
                  aria-label="Last name"
                />
              </div>
            </div>

            <div className={styles.formLine}>
              <div className={styles.formGroup}>
                <label className={`${styles.label} ${emailFocus || email.length ? styles.labelFocus : ''}`} htmlFor="email">Email</label>
                <input 
                  className={styles.input}
                  ref={emailRef} 
                  value={email} 
                  onChange={handleChange} 
                  onFocus={() => onEmailFocus(true)}
                  onBlur={() => onEmailFocus(false)}
                  type="email" 
                  name="email"
                  required
                  aria-label="Email"
                />
              </div>
              <div className={styles.formGroup}>
                <label className={`${styles.label} ${phoneFocus || phone.length ? styles.labelFocus : ''}`} htmlFor="phone">Phone (optional)</label>
                <input 
                  className={styles.input}
                  ref={phoneRef} 
                  value={phone} 
                  onChange={handleChange} 
                  onFocus={() => onPhoneFocus(true)}
                  onBlur={() => onPhoneFocus(false)}
                  type="number" 
                  name="phone"
                  aria-label="Phone"
                />
              </div>
            </div>
            <div className={styles.formArea}>
              <label className={`${styles.label} ${textFocus || text.length ? styles.labelFocus : ''}`} htmlFor="text">Text here</label>
              <textarea 
                className={styles.input}
                ref={textRef} 
                value={text} 
                onChange={handleChange} 
                onFocus={() => onTextFocus(true)}
                onBlur={() => onTextFocus(false)}
                type="text" 
                name="text"
                aria-label="Your text"
              />
            </div>
            <div className={styles.agreeWrap}>
              <input 
                className={styles.checkbox}
                value={checkbox}
                onChange={() => onCheckbox(!checkbox)}
                type="checkbox" 
                id="checkbox_1"
                aria-label="Agree with the rules"
              />
              <label htmlFor="checkbox_1"></label>
              <span>I give my consent for Eureka to process my personal data pursuant to Eureka Privacy and Cookies Policy in order to handle my request and respond to it and agree that, due to the international presence of Eureka, such processing may take place in a jurisdiction different from my home jurisdiction.</span>
            </div>
            <div className={styles.buttonsWrap}>
              {/* <input
                className={styles.upload}
                type="file"
                accept="image/*"
                name="data"
              /> */}
              <button disabled={!checkbox} className={styles.send}>Send</button>
            </div>
          </form>
          <div className={styles.response}>
            {sendSuccess === 1 && <span>Your message was successfully sent!</span>}
            {sendSuccess === 2 && <span className={styles.responseError}>Sorry, something went wrong, try again.</span>}
          </div>
        </div>
      </div>
    </section>
  )
};

export default Contacts;
