const MainScreen = ({ mobile }) => {
  const settings = {
    lazyLoad: 'ondemand',
    dots: true,
    arrows: true,
    speed: 1000,
    autoplaySpeed: 4000,
    infinite: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,    
    responsive: [
      {
        breakpoint: 769,
        settings: {
          arrows: false,
        },
      },
    ],
  };

  const showData = mobile ? dataMobile : data;

  return (
    <section className={styles.wrapper}>
      <Slider {...settings}>
        {showData.map(({ title, description, descriptionList, img }, index) => (
          <div key={index}>
            <div style={{background: `url('${img}') center / cover`, minHeight: '100vh'}}>
              <div className={styles.slide}>
                <h2 className={styles.title}>{title}</h2>
                <div>
                  {description.map((item, i) => <p className={styles.description} key={i}>{item}</p>)}
                </div>
                {descriptionList &&
                  <ul className={styles.descriptionListWrap}>
                    {descriptionList.map((item, i) => <li className={styles.descriptionList} key={i}>{item}</li>)}
                  </ul>
                }
              </div>
            </div>
          </div>
        ))}
      </Slider>
      <div  className={styles.bottomWrap}>
        <div className={styles.arrowWrap}>
          <a className={styles.arrow} href="#company" aria-label="Scroll down">
            <span className={styles.arrowDown}></span>
          </a>
          <span className={styles.arrowText}>Scroll Down</span>
        </div>
        <a className={styles.link} href="#contact">Contact us</a>
      </div>
    </section>
  );
};

export default MainScreen;
