import React from 'react';

import Slider from 'react-slick';
import 'slick-carousel/slick/slick-theme.css';
import 'slick-carousel/slick/slick.css';

import HTP from '../../images/brends/HTP.svg';
import NetworkBelarus from '../../images/brends/NetworkBelarus.svg';
import BestIt from '../../images/brends/BestIt.png';
import Clutch from '../../images/brends/Clutch.png';
import Clutch2 from '../../images/brends/Clutch2.png';
import ISO900 from '../../images/brends/ISO900.png';
import TopRated from '../../images/brends/TopRated.png';

import styles from './PartnersLogo.module.scss';

const data = [
  {
    title: 'Network Belarus',
    img: NetworkBelarus,
  },
  {
    title: 'HTP Belarus',
    img: HTP,
  },
  {
    title: 'TOP Rated Softwareworld',
    img: TopRated,
  },
  {
    title: 'clutch 2015',
    img: Clutch,
  },
  {
    title: 'clutch 2017',
    img: Clutch2,
  },
  {
    title: 'The Best IT Company',
    img: BestIt,
  },
  {
    title: 'PECB Certified',
    img: ISO900,
  },
];

const PartnersLogo = () => {
  const settings = {
    dots: false,
    arrows: false,
    speed: 1000,
    autoplaySpeed: 2000,
    infinite: true,
    slidesToShow: 7,
    slidesToScroll: 1,
    autoplay: true,
    responsive: [
      {
        breakpoint: 1025,
        settings: {
          slidesToShow: 5,
        },
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 4,
        },
      },
      {
        breakpoint: 500,
        settings: {
          slidesToShow: 2,
        },
      },
    ],
  };

  return (
    <section className={styles.wrapper}>
      <div className={styles.content}>
        <Slider {...settings}>
          {data.map(({ img, title }, index) => (
            <div key={index}>
              <div className={styles.imgWrap}>
                <img className={styles.img} src={img} alt={title} />
              </div>
            </div>
          ))}
        </Slider>
      </div>
    </section>
  );
};

export default PartnersLogo;
