import React from 'react';

import Jonson from '../../images/clients/Jonson.svg';
import Sberbank from '../../images/clients/Sberbank.svg';
import Samsung from '../../images/clients/Samsung.svg';
import Evernote from '../../images/clients/Evernote.svg';
import Raiffezen from '../../images/clients/Raiffezen.svg';
import World_Bank from '../../images/clients/World_Bank.svg';
import Iriscam from '../../images/clients/Iriscam.svg';
import Movado from '../../images/clients/Movado.svg';
import Olow from '../../images/clients/Olow.svg';
import STC from '../../images/clients/STC.svg';
import Unifi from '../../images/clients/Unifi.svg';
import Volkswagen from '../../images/clients/Volkswagen.svg';
import BSB from '../../images/clients/BSB.png';
import Enecsys from '../../images/clients/Enecsys.png';
import Roomster from '../../images/clients/Roomster.png';
import Securolytics from '../../images/clients/Securolytics.png';
import SST from '../../images/clients/SST.png';
import Telmie from '../../images/clients/Telmie.png';
import Unifi_2 from '../../images/clients/Unifi.png';
import Uptown from '../../images/clients/Uptown.png';

import styles from './Clients.module.scss';

const data = [
  {
    title: 'johnson & johnson',
    img: Jonson,
  },
  {
    title: 'Sberbank',
    img: Sberbank,
  },
  {
    title: 'Samsung',
    img: Samsung,
  },
  {
    title: 'Evernote',
    img: Evernote,
  },
  {
    title: 'Raiffeisen bank',
    img: Raiffezen,
  },
  {
    title: 'The World Bank',
    img: World_Bank,
  },
  {
    title: 'Movado Group',
    img: Movado,
  },
  {
    title: 'STC',
    img: STC,
  },
  {
    title: 'Unifi',
    img: Unifi,
  },
  {
    title: 'Volkswagen',
    img: Volkswagen,
  },
  {
    title: 'Securolytics',
    img: Securolytics,
  },
  {
    title: 'BSB Bank',
    img: BSB,
  },
  {
    title: 'Olow',
    img: Olow,
  },
  {
    title: 'Roomster',
    img: Roomster,
  },
  {
    title: 'Iris CRM',
    img: Iriscam,
  },
  {
    title: 'SST NET',
    img: SST,
  },
  {
    title: 'Unifi.id',
    img: Unifi_2,
  },
  {
    title: 'Enecsys',
    img: Enecsys,
  },
  {
    title: 'Uptown',
    img: Uptown,
  },
  {
    title: 'Telmie',
    img: Telmie,
  },
];

const Clients = () => (
  <>
    <section className={styles.wrapper}>
      <div className={styles.container}>
        <h2 className={`${styles.title} ${styles.sectionTitle}`}>Our Mission</h2>
        <p className={styles.description}>Our mission is to help start-ups and new business enterprises to materialize their ideas through affordable yet high-quality tools, new technologies and skills. To facilitate optimization, growth and expansion of the established businesses though reliable customized software solutions, niche skills, latest technologies and ongoing support.</p>
      </div>
    </section>
    <section className={styles.clientsWrap}>
      <div className={styles.container}>
        <h2 className={`${styles.titleTwo} ${styles.sectionTitle}`}>Clients</h2>
        <div className={styles.logoWrap}>
          {data.map(({ title, img }, i) => (
            <img className={styles.logoImg} src={img} alt={title} key={i} />
          ))}
        </div>
      </div>
    </section>
  </>
);

export default Clients;
