const Services = () => {
  const [tabs, toogleTabActive] = useState(data);
  const [showModal, toogleModal] = useState(false);
  const [itemActive, toogleItemActive] = useState(0);

  const modalData = tabs.filter(({id}) => id === itemActive);
  const openModal = id => {
    toogleModal(true);
    toogleItemActive(id);
  };

  const openModalMobile = id => {
    tabs.map(item => {
      if (item.id === id) {
        item.mobile = !item.mobile;
      } else {
        item.mobile = false;
      }
      return item;
    });

    toogleTabActive([...tabs])
  };

  return (
    <section className={styles.wrapper} id="services">
      <div className={styles.content} >
        <div className={styles.contentTop}>
          <h2 className={`${styles.title} ${styles.sectionTitle}`}>Services</h2>
          <p className={styles.subTitle}>Eureka offers a full set of services for web and mobile app development.</p>
          <p className={styles.description}>We provide customized solutions and consistent software development processes for our clients to excel in their business objectives and fulfil their project requirements. Regardless of the size or complexity of the project, our software outsourcing methodology allows us to expand our team and introduce additional niche skills to the project. You can trust our team of skilled IT specialists to look after your projects and meet deadlines at every stage – from discovery and concept creation to prototyping, design, development and testing.</p>
        </div>
        <div className={styles.list}>
          {tabs.map(({id, icon, title, text, services, mobile}) => (
            <div className={`${styles.item} ${mobile ? styles.itemActive : ''}`} key={id}>
              <div className={styles.contentWrap}>
                <div>
                  <img src={icon} alt={`Icon - ${title}`}/>
                  <p className={styles.itemTitle}>{title}</p>
                </div>
                <button 
                  className={`${styles.itemButtonMobile} ${mobile ? styles.itemButtonActive : ''}`}
                  onClick={() => openModalMobile(id)}
                >
                  <img src={Arrow} alt="Show details" />
                </button>
              </div>
              <button 
                className={`${styles.itemButton} ${showModal && styles.itemButtonHidden}`}
                onClick={() => openModal(id)}
              >learn more</button>

               {/* Mobile tabs */}
              <div className={`${styles.modalItemMobile} ${mobile ? styles.showItemMobile : ''}`} key={id}>
                {text.map((item, i) => <p className={styles.textItem} key={i}>{item}</p>)}
                <ul className={styles.servicesWrap}>
                  {services.map((item, i) => <li className={styles.servicesItem} key={i}>{item}</li>)}
                </ul>
              </div>
            </div>
          ))}

          {/* Desctop tabs */}
          <div className={`${styles.modal} ${showModal ? styles.showModal : ''}`}>
            <button 
              onClick={() => toogleModal(false)}
              className={styles.close}
            />
            <div className={styles.modalItemWrap}>
              {modalData.map(({id, icon, title, text, services}) => (
                <div className={styles.modalItem} key={id}>
                  <img src={icon} alt={`Icon - ${title}`}/>
                  <p className={styles.modalTitle}>{title}</p>
                  <div className={styles.textWrap}>
                    {text.map((item, i) => <p className={styles.textItem} key={i}>{item}</p>)}
                  </div>
                  <ul className={styles.servicesWrap}>
                    {services.map((item, i) => <li className={styles.servicesItem} key={i}>{item}</li>)}
                  </ul>
                </div>
              ))}
            </div>
        </div>
        </div>
      </div>
    </section>
  )
};

export default Services;
