import React, { useState, useEffect } from 'react';

import Logo from '../../images/logo.svg';
import styles from './Header.module.scss';

const data = [
  {
    link: '#company',
    title: 'Company',
  },
  {
    link: '#services',
    title: 'Services',
  },
  {
    link: '#technologies',
    title: 'Technologies',
  },
  {
    link: '#portfolio',
    title: 'Portfolio',
  },
  {
    link: '#contact',
    title: 'Contact us',
  },
];



const Header = () => {
  const [size, getSizeScroll] = useState(0);
  const [mobileOpen, onMobileOpen] = useState(false);

  useEffect(() => { 
    window.addEventListener('scroll', () => getSizeScroll(window.pageYOffset))
  });
  
  return (
    <header className={`${styles.header} ${size > 0 ? styles.headerBg : ''}`} id="header">
      <button className={styles.logoWrap} onClick={() => window.scrollTo(0,0)}>
        <img src={Logo} alt="Logo Eureka"/>
      </button>
      <button 
        className={`${styles.iconBurder} ${mobileOpen ? styles.iconBurderOpen : ''}`}
        onClick={() => onMobileOpen(!mobileOpen)}
        aria-label="Mobile menu"
      >
        <span></span>
        <span></span>
        <span></span>
      </button>
      <nav className={`${styles.nav} ${mobileOpen ? styles.navMobile : ''}`}>
        <ul className={styles.listWrap}>
          {data.map(({link, title}, i) => (
            <li className={styles.item} key={i}>
              <a 
                className={styles.link} 
                href={link}
                onClick={() => onMobileOpen(false)}
              >{title}</a>
            </li>
          ))}
        </ul>
      </nav>
    </header>
  )
};

export default Header;
