const Portfolio = () => {
  const [cards, onOpenCard] = useState(data);
  const [countShowCards, onChangeCount] = useState(4);

  const toogleCard = id => {
    cards.map(item => item.id === id ? item.active = !item.active : '');

    onOpenCard([...cards]);
  };

  const showingCards = cards.slice(0, countShowCards);

  const showList = ({ title, items }) => (
    <>
      <h4>{title}</h4>
      <ul>
        {items && items.map((item, i) => <li key={i}>{item}</li>)}
      </ul>
    </>
  );
 
  return (
    <section className={styles.wrapper} id="portfolio">
      <h2 className={`${styles.title} ${styles.sectionTitle}`}>Portfolio</h2>
      <div className={styles.cards}>
        {showingCards.map(({id, title, active, text, categories, img, descriptionLeft, descriptionRight, list_1 = [], list_2 = [] }, i) => (
          <div className={styles.card} key={i}>
            <div className={`${styles.cardContent} ${active ? styles.cardContentHidden : ''}`}>
              <img  className={styles.mobileImg} src={img} alt={img} />
              <div className={styles.cardInner}>
                <h3 className={styles.cardTitle}>{title}</h3>
                <div className={styles.categories}>
                  {categories.map((item, i) => 
                    <span className={styles.category} key={i}>{item}</span>
                  )}
                </div>
                <p className={styles.cardText}>{text}</p>
                <button 
                  className={`${styles.cardBtn} ${styles.showDescription} ${active && styles.cardBtnHidden}`} 
                  onClick={() => toogleCard(id)}
                >open the case</button>
                <button 
                  className={`${styles.itemButtonMobile} ${active ? styles.itemButtonActive : ''}`}
                  onClick={() => toogleCard(id)}
                >
                  <img src={Arrow} alt="Show details" />
                </button>
              </div>
            </div>
            <div style={{background: `url('${img}') center / cover`}} className={styles.img} />

            {/* Modal */}
            <div className={`${styles.descriptionCard} ${active ? styles.descriptionCardActive : ''}`}>
              <div className={styles.colWrap}>
                <h3 className={`${styles.cardTitle} ${styles.cardTitleModal}`}>{title}</h3>
                {descriptionLeft.map((item, i) => <p className={styles.descriptionItem} key={i}>{item}</p>)}
              </div>
              <div className={styles.colWrap}>
                {descriptionRight.map((item, i) =>
                  <p className={`${styles.descriptionItem} ${styles.descriptionItemRight}`} key={i}>{item}</p>
                )}
                {list_1.technologies 
                  ? <>
                      <h4>{list_1.title}</h4>
                      <ul>
                        {list_1.technologies.map((item, i) => <li key={i}>{showList(item)}</li>)}
                      </ul>
                    </>
                  : showList(list_1)
                }
                {showList(list_2)}
              </div>
              <button 
                onClick={() => toogleCard(id)}
                className={`${styles.close} ${!active && styles.cardBtnHidden}`}
              />
            </div>
            {/* End Modal */}
          </div>
        ))}
      </div>
      {countShowCards < cards.length &&
        <button 
          className={`${styles.cardBtn} ${styles.showMore}`}
          onClick={() => onChangeCount(countShowCards + 4)}
        >Load more</button>
      }
    </section>
  )
};

export default Portfolio;
