import React from 'react';

import ChooseUs_1 from '../../images/icons/chooseUs-1.svg';
import ChooseUs_2 from '../../images/icons/chooseUs-2.svg';
import ChooseUs_3 from '../../images/icons/chooseUs-3.svg';
import ChooseUs_4 from '../../images/icons/chooseUs-4.svg';
import ChooseUs_5 from '../../images/icons/chooseUs-5.svg';
import ChooseUs_6 from '../../images/icons/chooseUs-6.svg';
import styles from './WhyClientsChoose.module.scss';

const data = [
  {
    icon: ChooseUs_1,
    title: 'Versatile',
    text: 'Our unique business and partnership model allows us to combine professional expertise from traditional IT sectors with specific knowledge and skills from niche industries. This provides us with the grounds to excel where other companies cannot.',
  },{
    icon: ChooseUs_2,
    title: 'Accessible and affordable',
    text: 'We approach each client and project individually. By analysing your needs and primary information at an early stage, we define the best process and combination of skills and techniques that will lead to the success of the project effectively and at a reasonable price.',
  },{
    icon: ChooseUs_3,
    title: 'Approachable',
    text: 'From the very beginning and throughout the entire process you are supported by our team of experienced project managers and business analysts as if they were working in the same room as you. We provide full transparency in every stage of your project.',
  },{
    icon: ChooseUs_4,
    title: 'Award-winning',
    text: 'Our highly qualified software developers and project managers have won numerous awards and received professional recognition from established agencies such as Clutch, CrunchBase, Upwork, SoftwareWorld to name but a few.',
  },{
    icon: ChooseUs_5,
    title: 'Adaptable',
    text: 'We remain open and flexible to adopting new technologies, approaches and are happy to integrate our clients’ approach into the workflow.',
  },{
    icon: ChooseUs_6,
    title: 'Local',
    text: 'We are one of the very few outsourcing companies from Eastern Europe with a full-time presence in the UK. We are in every sense of the word – your Local Outsourcing IT Company.',
  },
];

const WhyClientsChoose = () => (
  <section className={styles.wrapper}>
    <div className={styles.content}>
      <h2 className={`${styles.title} ${styles.sectionTitle}`}>Why clients choose us?</h2>
      <h2 className={`${styles.titleMobile} ${styles.sectionTitle}`}>Characteristics</h2>
      <div className={styles.list}>
        {data.map(({icon, title, text}, i) => (
          <div className={styles.item} key={i}>
            <img className={styles.itemIcon} src={icon} alt={`icon - ${title}`} />
            <div className={styles.itemContetnt}>
              <p className={styles.itemTitle}>{title}</p>
              <p className={styles.itemText}>{text}</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  </section>
);

export default WhyClientsChoose;
